import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

const login = (resolve) => {
  import('components/pages/login').then((module) => {
    resolve(module)
  })
}

const register = (resolve) => {
  import('components/pages/register').then((module) => {
    resolve(module)
  })
}
const home = (resolve) => {
  import('components/public/home').then((module) => {
    resolve(module)
  })
}
const index = (resolve) => {
  import('components/pages/index').then((module) => {
    resolve(module)
  })
}
const adManage = (resolve) => {
  import('components/pages/adManage').then((module) => {
    resolve(module)
  })
}

const moneyRecord = (resolve) => {
  import('components/pages/moneyRecord').then((module) => {
    resolve(module)
  })
}
const alterInfo = (resolve) => {
  import('components/pages/alterInfo').then((module) => {
    resolve(module)
  })
}


export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/login'
    },
    {
      path:'/login',
      component:login
    },
    {
      path:'/register',
      component:register
    },
    {
      path:'/index',
      component: home,
      children:[
        {
          path: '/index',
          component: index,
          meta:{requireAuth:true}
        },
        {
          path:'/adManage',
          component:adManage,
          meta:{requireAuth:true}
        },
        {
          path:'/moneyRecord',
          component: moneyRecord,
          meta:{requireAuth:true}
        },
        {
          path:'/alterInfo',
          component:alterInfo,
          meta:{requireAuth:true}
        }
      ]
    }
  ]
})



